.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.navbar {
  display: grid;
  grid-template-columns: 60% 40%;
  padding: 0 8px;
  justify-items: stretch;
  background-color: #63666c;

}


.navlist {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-right: 20%;
  font-family: cursive, sans-serif;
  font-style: italic;
  font-weight: 300;
  height: 2px;
}


.navlist li {
  list-style: none;
  padding: 10px;
}

.navlist li a {
  text-align: center;
  text-decoration: none;
  font-size: 24px;
  color: #000000;
  user-select: none;
}

.navlist li a:hover {
  font-size: 26px;
  color: #fff;
  font-weight: 400;
}

.login {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.login button {
  border: 2px solid aqua;
  border-radius: 5px;
  font-size: 15px;
  height: 25px;
  width: 4rem;
  font-family: Arial, Helvetica, sans-serif;
  cursor: pointer;
}

.login button:hover {
  background-color: black;
  color: white;
  height: 30px;
  width: 5rem;
  box-shadow: 2px 3px 1px #000;
}

.logo {
  display: flex;
  align-items: center;
}

.logo img {
  height: 30px;
  width: 35px;
  overflow: hidden;
  border-radius: 15%;
  filter: invert(1);
  margin: 0px 3px;
}

.logo span {
  background: linear-gradient(to right bottom, #00c7ff, #ff00f7);
  font-weight: 800;
  font-size: 22px;
  background-clip: inherit;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  user-select: none;
}

.container {
  width: 45vw;
  margin: 35px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  }

.container textarea {
  font: 1em sans-serif;
  background-color: #c1caff;
  height: 200px;
  width: inherit;
  color: #000;
  border-radius: 5px;
  
}

.btn {
  cursor: pointer;
  font-weight: 600;
  margin: 20px 10px;
  height: 25px;
  outline: none;
  border: none;
  border-radius: 10px;
  box-shadow: 4px 4px 2px grey;
  user-select: none;
}

.btn:hover {
  border-bottom: 2px solid #808080;
  border-right: 2px solid #808080;
  box-shadow: inset 2px 2px 2px #808080;
}

.container_2{
  width: 60vw;
  margin: 35px auto;
}
